.navbar-list {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
  width: 30%;
  margin: auto;
  padding: 0;
}

@import url("https://fonts.googleapis.com/css?family=Open+Sans");
.navbar-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px;
  font-size: 24px;
  text-decoration: none;
  font-family: Open Sans;
  color: black;
  transition: background-color 0.3s ease;
  border-radius: 10px;
  font-weight: bold;
}

.navbar-link:hover {
  background-color: black;
  color: white;
  border-radius: 10px;
  text-decoration: none;
}

.profiles-icon:hover {
  filter: brightness(150%);
}

@media screen and (max-width: 600px) {
  .navbar-list {
    width: 100%;
  }

  .navbar-link {
    font-size: 20px;
  }
}
