.projects {
  width: 80%;
  margin: auto;
  display: block;
  text-align: center;
}

.project {
  width: 90%;
  margin: 0 auto;
  text-align: center;
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.project > * {
  width: 100%;
  margin: 0.5rem 0;
}

.project img {
  border-radius: 5%;
}

.project img.hovered {
  transform: scale(1.2);
  filter: brightness(0.7);
  transition: transform 0.3s ease-in-out;
  max-width: 100%;
  max-height: 100%;
}
.project .description {
  display: none;
}

.project:hover img {
  transform: scale(1.1);
  filter: brightness(0.7);
}

.project:hover .description {
  font-family: Open Sans;
  display: block;
  margin-top: 50px;
  font-weight: bold;
}

@import url(https://fonts.googleapis.com/css?family=Open+Sans:regular);
.p-link {
  margin-top: 50px;
  margin-bottom: 40px;
  font-family: Open Sans;
  font-weight: bold;
}

.link {
  font-size: 26px;
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.page-title {
  font-family: "Open Sans", sans-serif;
  text-align: center;
  font-size: 36px;
  font-weight: bold;
  margin-top: 50px;
}

@media (max-width: 768px) {
  .page-title {
    width: 80%;
    margin: auto;
    font-size: 26px;
  }

  .projects {
    display: block;
  }
  .project {
    width: 90%;
    margin: 0 auto;
    text-align: center;
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .project > * {
    width: 100%;
    margin: 0.5rem 0;
  }
  .project img.hovered {
    transform: none;
  }
  .project:hover img {
    transform: none;
  }
}
