.intro {
  text-align: center;
}

.intro-title {
  font-family: "Open Sans", sans-serif;
  margin-top: 50px;
}

.foto-profilo {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.info {
  text-align: center;
  margin-top: 100px;
  margin-bottom: 200px;
  font-size: 26px;
}

.info-title {
  font-family: "Open Sans", sans-serif;
  font-size: 46px;
}

@import url("https://fonts.googleapis.com/css?family=Open+Sans");
.about-me {
  width: 80%;
  font-family: "Open Sans", sans-serif;
  font-size: 1.1em;
  line-height: 1.5;
  margin: auto;
  padding: 2em;
  background-color: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.bold {
  color: black;
  font-weight: bold;
}

@media only screen and (max-width: 768px) {
  .intro {
    margin-top: 50px;
  }

  .foto-profilo {
    width: 150px;
    height: 150px;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .info {
    margin-top: 50px;
    margin-bottom: 100px;
    font-size: 20px;
  }

  .info-title {
    font-size: 36px;
  }
  .about-me {
    font-size: 20px;
  }
}

/* Regole di stile per dispositivi con larghezza massima di 480px */
@media only screen and (max-width: 480px) {
  .intro {
    margin-top: 30px;
  }

  .foto-profilo {
    width: 100px;
    height: 100px;
    margin-top: 5px;
    margin-bottom: 5px;
  }

  .info {
    margin-top: 30px;
    margin-bottom: 50px;
    font-size: 16px;
  }

  .info-title {
    font-size: 24px;
  }
  .about-me {
    font-size: 18px;
  }
}
